import React from "react"
import { Form, Input, Button, message } from "antd"
import { compareStrings } from "../../../utils"

const Question = ({ question, hint, answers, onDone }) => {
  const [form] = Form.useForm()

  const onFinish = (values) => {
    const ans = answers.split(",")
    if (ans.find((a) => compareStrings(a, values.answer) > 0.95)) {
      onDone()
    } else {
      if (ans.find((a) => compareStrings(a, values.answer) > 0.9)) {
        message.error("Your Answer is Close!")
      } else {
        message.error("Incorrect answer, try again!")
        form.setFieldsValue({ answer: undefined })
      }
    }
  }

  return (
    <div
      style={{
        display: "flex",
        padding: "20px",
        alignContent: "center",
        justifyContent: "center",
        textAlign: "center",
        flexDirection: "column",
        height: "100vh",
        background: "#f7f7f7",
      }}
    >
      <div style={{ maxWidth: 500, margin: "auto" }}>
        <h1>{question}</h1>
        <h4>{hint}</h4>
        <Form form={form} onFinish={onFinish}>
          <Form.Item
            name="answer"
            rules={[
              {
                required: true,
                message: "Please input your answer!",
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit">
              Take a guess!
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  )
}

export default Question
